.popup__container {
    position: relative;
    display: flex;
    flex-direction: column;
    background-color: #ffffff;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    padding: 34px 36px 37px 36px;
    max-width: 430px;
    width: 100%;
    box-sizing: border-box;
}

@media screen and (max-width: 620px) {
    .popup__container {
        padding: 25px 22px;

    }
}